.section3 {
  height: 900px;
  text-align: center;
}

.proja1 {
  position: absolute;
  z-index: 0;
  height: 700px;
  width: auto;
  margin-bottom: 20%;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
  align-items: center;
}


[class^="BodyA-"] {
  position: relative;
  margin-top: 70px;
  display: inline-block;
  max-width: 800px;
  margin-left: 194px;
  z-index: 1;
  padding: 15px;
  height: 145px;
  width: 375px;
  left: 11%;
  top: 0;
  font-family: "Arial";
  text-align: left;
  background-color: #FEBA30;
  border-radius: 15px;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.fade-in-section {
  opacity: 0;
  transform: translateX(10vh);
  visibility: hidden;
  -webkit-transition: opacity 1000ms ease-out, transform 600ms ease-in,
    visibility 1200ms ease-out;
  will-change: opacity, transform, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}
